.main-nav {
  width: 100%;
}

.main-nav__toggle {
    width: 24px;
    height: 18px;
    background: url('../img/icon-mobile-menu.svg') no-repeat;
    border: none;
    margin-left: auto;
}

@media (min-width: $tablet-width) {
  .main-nav__toggle {
    display: none;
  }

  .main-nav {
    width: 440px;
  }
}

@media (min-width: $desktop-width) {
  .main-nav {
    width: 480px;
    position: relative;
  }
}
